.menu {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 50px;
  left: 0;
  transform: translateX(-130%);
  transition: all 0.4s;
}

.menu.active {
  transform: translateX(0);
}

.blur {
  width: 100%;
  height: 100vh;
  backdrop-filter: blur(2px);
  position: absolute;
  left: 30%;
}

.menu__content {
  width: 30%;
  height: 100%;
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: all 0.6s;
}

.menu__header {
  font-size: 2rem;
  color: white;
  padding: 10px;
  border-bottom: 1px solid white;
  width: 100%;
  text-align: center;
}

ul li a {
  color: white;
  text-decoration: none;
  font-size: 1.3rem;
}

ul li {
  display: flex;
  align-items: center;
}

ul {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.material-icons {
  color: white;
  margin-left: 10px;
}

@media screen and (max-width: 768px) {
  .menu__content {
    width: 100%;
  }

  .blur {
    backdrop-filter: none;
  }    
}